export type edge = {
    id: number;
    v1: number;
    v2: number;
    weight: number;
    selected: boolean;
};

export type graph = {
    nVertices: number;
    nEdges: number;
    edges: edge[];
};

export function createAdjMatrix (g: graph){
    const matrix: number[][] = [];
    for (const link of g.edges){
        const v = link.v1;
        const w = link.v2;
        matrix[v][w] = link.weight;
        matrix[w][v] = link.weight;
    }
    

}

let pred = [];

export function connectivityCheck(g: graph, sel: number[]){
    if(sel.length == 0){
        return false;
    }
    const adj: number[][] = [];
    for (let i = 0; i < g.nVertices; i++){
        adj.push([]);
    }
    // create adjacency matrix for the selected subgraph
    for (let i = 0; i < sel.length; i++){
        const v1 = g.edges[sel[i]].v1;
        const v2 = g.edges[sel[i]].v2;
        adj[v1].push(v2);
        adj[v2].push(v1);
    }

    const origin = g.edges[sel[0]].v1;
    const visited: boolean[] = Array<boolean>();
    // Pre-populate array:
    for(let i = 0; i < g.nVertices; i++){
        visited.push(false);
    }
    // run dfs to check connectivity
    const s: number[] = [];
    visited[origin] = true;
    s.push(origin);
    while(s.length > 0){
        const v = s.pop();
        for(const adjV of adj[v])
        {
            if(!visited[adjV])
            {
                visited[adjV] = true;
                s.push(adjV);
            }
        }
    }

    let connected = true;
    for(let i = 0; i < visited.length; i ++){
        const inGraph = adj[i].length > 0;
        if (!visited[i] && inGraph){
            connected = false;
        }
    }
    return connected;
}


export function checkValidity(g: graph, sel: number[], origin: number, sink: number){
    const nodeCount: number[] = new Array(g.nVertices);
    nodeCount.fill(0);
    // fill nodeCount with counts for each node in the selected edges, index is node number

    for(let i = 0; i < sel.length; i ++){
        const index = sel[i];
        const v1 = g.edges[index].v1;
        const v2 = g.edges[index].v2;
        nodeCount[v1]++;
        nodeCount[v2]++;
    }
    const a = nodeCount[origin];
    const b = nodeCount[sink];
    const ones = (nodeCount.filter(e => e == 1)).length;
    const twos = (nodeCount.filter(e => e == 2)).length;
    const threes = (nodeCount.filter(e => e == 3)).length;
    const fours = (nodeCount.filter(e => e == 4)).length;
    const big = (nodeCount.filter(e => e > 4)).length;

    let ret: number;
    // one outgoing edge from origin and one incoming edge to sink
    const sourceToSink = (nodeCount[origin] == 1 && nodeCount[sink] == 1);
    // no edges have been selected
    const empty = sel.length == 0;
    // connected check via dfs 
    const connected = connectivityCheck(g, sel);
    // in and out cycle in path
    const inOutCycle = ones == 2 && fours == 2 && threes == 0 && big == 0;
    // cycle and with walking the same path twice 
    const cycle = (threes > 0) && (threes % 2 == 0);


    if (empty){ // nothing has been selected
        ret = 0;
        console.log("noch nichts ausgewählt");
        
    } else if (a == 0 || b == 0){ // either origin or sink are not visited
        ret = 1
        console.log("du den start oder endpunkt nicht zu besuchen");
        
    } else if (!connected){ // selected edges are not conneted
        ret = 2;
        console.log("dein Weg scheint nicht verbunden zu sein");
        
    } else if (twos == sel.length && a == 2 && b == 2){ // one cycle containing source and waypoint -> roundtrip
        ret = 3;
        console.log("dein Weg ist ein zyklus");
        
    } else if (twos == sel.length){ // one cycle missing either source or waypoint -> roundtrip 
        ret = 4;
        console.log("dein weg ist ein zyklus");
        
    } else if (cycle || inOutCycle){ // cycle somwhere in the path
        ret = 5;
        console.log("du besuchst einen Ort mehrmals");
        
    } else if (ones > 2){ // tree
        ret = 6;
        console.log("du scheinst mehrere endpunkte zu haben");
    
    } else if (threes > 0 || fours > 0 || big > 0){ // something weird with a cycle
        ret = 7;
        console.log("du besuchst einen ort mehrmals");
        
    } else if (!sourceToSink){ // start or end node wrong
        ret = 8;
        console.log("dein start und ziel scheint nicht zu stimmen");
        
    } else { // correct
        ret = 9;
    }

    return ret;
}
