export function getAllShortestPaths(g: graph, origin: number){
    const paths = new Set();
    for (let i = 0; i < g.nVertices; i++){
        if (i == origin){
            continue;
        }
        dijkstra(g, origin, i);
        const curr = getShortestPath(g, origin, i);
        paths.add(curr);
    }
    
    return paths;
}

export function createAdj(g: graph){
    const adj = [];
    for (let i = 0; i < g.nVertices; i ++){
        adj.push([]);
    }
    for (let i = 0; i < g.nEdges; i ++){
        const v1 = g.edges[i].v1;
        const v2 = g.edges[i].v2;
        adj[v1].push(v2);
        adj[v2].push(v1);
    }
    return adj;
}


export function dp(g: graph, adj: number[][], d: number[][], vis: Set<number>[][], currMinute: number, mushrooms: number[]){
    const row: number[] = new Array(g.nVertices);
    row.fill(0);
    // i: current vertice
    for(let i = 0; i < g.nVertices; i++){
        let currMax = 0;
        let maxNeigh = -1;
        let maxEdge = 0;
        // neigh: current neighbour of i 
        // j: index of neigh in adj matrix[i]
        for (let j = 0; j < adj[i].length; j++){
            const neigh = adj[i][j];
            
            let edgeWeight = 0;
            // get edge weight from graph
            for(let x = 0; x < g.nEdges; x ++ ){
                const v1 = g.edges[x].v1;
                const v2 = g.edges[x].v2;
                if ((v1 == i && v2 == adj[i][j]) || (v2 == i && v1 == adj[i][j])){
                    
                    edgeWeight = g.edges[x].weight;
                    break;
                }
            }

            if (currMinute - edgeWeight < 0 || edgeWeight == 0 ){
                continue;
            }

            const prev = vis[currMinute - edgeWeight][neigh];
            const found = prev.has(i);
            // for(let k = 0; k < prev.length; k++){
            //     if (prev[k] == i){
            //         found = true;
            //         break;
            //     }
            // }
            if (prev.size == 0 || found){
                continue;
            }
            if (d[currMinute - edgeWeight][neigh] + mushrooms[i] > currMax){
                maxNeigh = neigh;
                maxEdge = edgeWeight;
                currMax = d[currMinute - edgeWeight][neigh] + mushrooms[i];
            }
        }

        row[i] = currMax;
        if (currMax > 0){
            
            for ( const item of vis[currMinute -  maxEdge][maxNeigh].values() ){
                vis[currMinute][i].add(item);
            }
            vis[currMinute][i].add(maxNeigh);
            
        }

    }

    return row;
}

export function mushroomTrip(g: graph, adj: number[][], origin: number, maxMinutes: number, mushrooms: number[], visited: Set<number>[][]){
    const d: number[][] = [];
    // const visited: boolean[] = Array<boolean>();
    // Pre-populate array:
    for (let i = 0; i <= maxMinutes; i ++){
        const a = new Array(g.nVertices);
        a.fill(0);
        d.push(a);
        visited[i] = new Array(g.nVertices);
    }
    
    for(let i = 0; i < g.nVertices; i++){
        // visited.push(false);
        for (let j = 0; j <= maxMinutes; j ++){
            visited[j][i] = new Set();
        }
    }

    visited[0][origin].add(-1);
    d[0][origin] = mushrooms[origin];
    for (let i = 1; i <= maxMinutes; i ++){
        d[i] = dp(g, adj, d, visited, i, mushrooms);
    }
    let maxMush = 0;
    let maxMin = 0;
    let maxVer = 0;
    for (let i = 0; i <= maxMinutes; i ++){
        for (let j = 0; j <=  g.nVertices; j ++ ){
            if (d[i][j] > maxMush){
                maxMush = d[i][j];
                maxMin = i;
                maxVer = j;
            }
        }
        
    }
    // console.log(maxMush);
    // console.log(visited[maxMin][maxVer]);
    // console.log(maxVer);
    const solutionSet = visited[maxMin][maxVer];
    solutionSet.delete(-1);
    solutionSet.add(maxVer);
    const sol: number[] = [];
    for(const item of solutionSet){
        sol.push(item);
    }
    sol.push(maxMush);

    return sol;
}


// formats input edges in path order and outputs nicely formated string
export function formatPath(g: graph, edgeNumbers: number[], origin: number, sink: number): string{
    let formated = "";
    const numNodes = [];
    const alphaNodes = [];

    numNodes.push(origin);
    let current = origin;
    
    while(current != sink){
        for (let i = 0; i < edgeNumbers.length; i++){
            if(edgeNumbers[i] < g.nEdges){
                const one = g.edges[edgeNumbers[i]].v1;
                const two = g.edges[edgeNumbers[i]].v2;
                if (current == one){
                    numNodes.push(two);
                    current = two;
                    edgeNumbers[i] = g.nEdges;
                    break;
                } else if (current == two){
                    numNodes.push(one);
                    current = one;
                    edgeNumbers[i] = g.nEdges;
                    break;
                }
            }
        }
        
    }
    for (let i = 0; i < numNodes.length; i++){
        alphaNodes.push(alphabet[numNodes[i]]);
    }

    for (let i = 0; i < alphaNodes.length -1 ; i++){
        formated += alphaNodes[i];
        formated += " - "
    }
    formated += alphaNodes[alphaNodes.length - 1];
    
    return formated;
    
}



